import React, {useState} from 'react';
import {Avatar, Button, Paper, TextField} from "@material-ui/core";
import {v1} from "uuid";
import axios from "axios";
import s from './user.module.css'

class Users extends React.Component {

    componentDidMount() {
        axios.get(`https://social-network.samuraijs.com/api/1.0/users?page=${this.props.currentPage}&count=${this.props.pageSize}`)
            .then(response => {
                console.log(response.data)
                this.props.setUsers(response.data.items)
                this.props.setTotalCounts(response.data.totalCount)
            })
    }

    onPageChanged = (p) => {
        this.props.setCurrentPage(p);
        axios.get(`https://social-network.samuraijs.com/api/1.0/users?page=${p}&count=${this.props.pageSize}`)
            .then(response => {
                this.props.setUsers(response.data.items)
            })
    }

    render = () => {
        const total = Math.ceil(this.props.totalUsers / this.props.pageSize)

        //
        return (
            <div>
                <div className={s.count}>

                    <Button variant="outlined" color="secondary" onClick={() => {
                        if (this.props.currentPage > 1) {
                            this.onPageChanged(this.props.currentPage - 1)
                        } else {
                            this.onPageChanged(1)
                        }
                    }}>back</Button>
                    <span className={s.span}>{this.props.currentPage} / {total}</span>
                    <TextField label={'Page number'}  type="text"  onChange={(e) => {

                        this.onPageChanged(e.currentTarget.value)
                    }}/>
                    <Button variant="outlined" color="secondary"
                            onClick={() => this.onPageChanged(this.props.currentPage + 1)}>next</Button>


                    {/*{pages.map((p) => {*/}
                    {/*    return (*/}

                    {/*        <span onClick={() => this.onPageChanged(p)}*/}
                    {/*              className={this.props.currentPage === p ? s.select : ''}>{p}</span>*/}
                    {/*    )*/}
                    {/*})}*/}

                </div>
                <div className={s.box}>

                    {
                        this.props.users.map((u) => {

                            return (

                                <Paper key={u.id} elevation={9} className={s.user_block}>
                                    <div className={s.first}>
                                        <div className={s.photo}>
                                            <Avatar
                                                src={u.photos.small === null ? 'https://yt3.ggpht.com/ytc/AAUvwnihIIjfolgDP46s4snHGuI9U1oY-gppxfU2l0YFNDc=s900-c-k-c0x00ffffff-no-rj' : u.photos.small}
                                                alt="" style={{height: '70px', width: '70px'}}/>
                                            <span>
                                <div className={s.name}>{u.name}</div>
                                <div>{u.status}</div>
                                 </span>
                                        </div>

                                        <div>
                                            {u.followed ?
                                                <Button variant="outlined" color="primary"
                                                        onClick={() => this.props.unfollowUser(u.id)}>Follow</Button> :
                                                <Button variant="outlined" color="secondary"
                                                        onClick={() => this.props.followUser(u.id)}>UnFollow</Button>}
                                        </div>

                                    </div>

                                    <div className={s.info}>
                            <span className={s.location}>
                                <div className={s.location_city}>{u.location ? u.location.city : 'Minsk'}</div>
                                <div className={s.location_country}>{u.location ? u.location.country : 'Belarus'}</div>
                            </span>
                                    </div>
                                </Paper>
                            )
                        })
                    }
                </div>
            </div>
        );
    }
}

export default Users;
